import { SVG, Container, Image, Path, Shape } from '@svgdotjs/svg.js';
import '@svgdotjs/svg.draggable.js';
import { saveSvgAsPng } from 'save-svg-as-png';
import {
  Component,
  OnInit,
  Input,
  ViewChild,
  ElementRef,
  ChangeDetectionStrategy,
  OnChanges,
  SimpleChanges,
  NgZone,
  OnDestroy,
  ChangeDetectorRef,
  EventEmitter,
  Output
} from '@angular/core';
import { fromEvent } from 'rxjs';
import { filter, map, throttleTime } from 'rxjs/operators';
import { SubSink } from 'subsink';
import {
  DrawDef,
  DrawTool,
  DrawToolKinds,
  DrawDataObject,
  DrawObject,
  DrawXY,
  DrawImageMap
} from '../../draw.models';
import { DrawHandler, DrawImageHandler, DrawBrushHandler, DrawArrowHandler } from './draw-handlers';
import { debug } from 'util';

@Component({
  selector: 'mdo-draw',
  templateUrl: './draw.component.html',
  styleUrls: ['./draw.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DrawComponent implements OnInit, OnChanges, OnDestroy {
  @Input() def: DrawDef;
  @Input() data: DrawDataObject[] = [
    // {
    //   toolId: 'red arrow',
    //   payload: 'M 418.25 180.72 L 629.08 524.94'
    // }
  ];
  @Output() dataChange = new EventEmitter<DrawDataObject[]>();

  @ViewChild('svg', { static: true }) svgRef: ElementRef;

  draw: Container;
  bgImage: Shape;
  background: string = null;
  activeTool: DrawTool = null;
  objects: DrawObject[] = [];

  toolHandlers: { [type: string]: DrawHandler } = {};
  private sub = new SubSink();

  constructor(private ngZone: NgZone, private changeDetector: ChangeDetectorRef) {}

  async ngOnInit() {
    if (!this.def) {
      throw new Error('DrawComponent [def] input of DrawDef type is required');
    }
    this.draw = this.createDrawSVG();

    const imageMap = await this.preloadImages(this.def);

    this.renderBg(this.def.background);

    this.createToolbar(imageMap);

    if (this.data) {
      this.renderData(this.data);
    }
  }

  createDrawSVG(): Container {
    const draw = SVG(this.svgRef.nativeElement) as Container;
    draw.size('100%', '100%');
    return draw;
  }

  createToolbar(imageMap: DrawImageMap) {
    this.bindEvents();
    const tool =
      typeof this.def.activeTool === 'number' ? this.def.toolbar[this.def.activeTool] : null;
    this.setTool(tool);

    this.renderBg(this.def.background);

    this.createToolHandlers(this.def.toolbar, this.draw, imageMap);

    this.changeDetector.markForCheck();
  }

  createToolHandlers(toolbar: DrawTool[], draw: Container, imageMap: DrawImageMap) {
    const handlerMapping = {
      [DrawToolKinds.IMAGE]: DrawImageHandler,
      [DrawToolKinds.BRUSH]: DrawBrushHandler,
      [DrawToolKinds.ARROW]: DrawArrowHandler
    };
    toolbar.forEach(tool => {
      this.toolHandlers[tool.toolId] = new handlerMapping[tool.kind](
        draw,
        tool,
        imageMap[tool.toolId]
      );
      this.sub.sink = this.toolHandlers[tool.toolId].onShape$.subscribe(_ => {
        this.addObject(_, tool.toolId);
        this.dumpData();
      });
    });
  }

  // mouse events outside angular
  bindEvents() {
    const bindings = {
      mouseenter: 'handleMouseEnter',
      mousemove: 'handleMouseMove',
      mousedown: 'handleMouseDown',
      mouseup: 'handleMouseUp',
      mouseleave: 'handleMouseLeave'
    };

    const bindFunc = () =>
      Object.entries(bindings).forEach(
        ([eventname, method]) =>
          (this.sub.sink = fromEvent(this.svgRef.nativeElement, eventname)
            .pipe(
              filter(_ => !!this.activeTool),
              throttleTime(15),
              map(event => {
                const { x, y } = this.mouseXYtoViewBox(event as MouseEvent);
                const handler = this.toolHandlers[this.activeTool.toolId];
                handler[method]({ x, y });
              })
            )
            .subscribe())
      );

    this.ngZone.runOutsideAngular(bindFunc);
  }

  mouseXYtoViewBox($event: MouseEvent): DrawXY {
    const rect = this.svgRef.nativeElement.getBoundingClientRect();
    const realLeft = $event.clientX - rect.left;
    const realTop = $event.clientY - rect.top;

    const viewbox = this.bgImage.bbox();
    const x = (viewbox.w / rect.width) * realLeft;
    const y = (viewbox.h / rect.height) * realTop;

    const round = (_: number) => Math.round(_ * 100) / 100;
    return { x: round(x), y: round(y) };
  }

  ngOnChanges(changes: SimpleChanges): void {}

  renderBg(backgroundUrl: string) {
    this.background = backgroundUrl;

    this.draw.clear();

    this.bgImage = this.draw.image(backgroundUrl, () => {
      const bbox = this.bgImage.bbox();
      this.draw.viewbox(`0 0 ${bbox.w} ${bbox.h}`);
    });
  }

  setTool(tool: DrawTool) {
    tool = tool === this.activeTool ? null : tool;
    this.activeTool = tool;
  }

  addObject(shape: Shape, toolId: string) {
    const outline = this.draw
      .rect()
      .fill('transparent')
      .draggable();

    const object: DrawObject = {
      shape,
      outline,
      toolId,
      selected: false
    };

    outline.on('beforedrag', e => {
      if (this.activeTool) {
        e.preventDefault();
      }
    });
    outline.on('dragmove', () => {
      this.positionByOutline(object);
    });

    outline.on('dragend', () => {
      this.dumpData();
    });

    outline.on('mousedown', () => {
      if (this.activeTool) {
        return;
      }
      this.select(object);
      this.changeDetector.detectChanges();
    });

    this.positionOutline(object);
    this.objects.push(object);
  }

  positionOutline(_: DrawObject) {
    const bbox = _.shape.bbox();
    _.outline.size(bbox.w + 20, bbox.h + 20);
    _.outline.move(bbox.x - 10, bbox.y - 10);
  }
  positionByOutline(_: DrawObject) {
    const bbox = _.outline.bbox();
    _.shape.move(bbox.x + 10, bbox.y + 10);
  }

  select = (_: DrawObject) => {
    this.objects.forEach(this.deselect);
    _.outline.stroke({
      color: '#987',
      dasharray: '5,5'
    });
    _.selected = true;
  };

  deselect = (_: DrawObject) => {
    _.outline.stroke('transparent');
    _.selected = false;
  };

  get deleteDisabled() {
    return this.objects.every(_ => !_.selected);
  }

  deleteObject() {
    this.objects.filter(_ => !!_.selected).forEach(this.removeObject);
    this.objects = this.objects.filter(_ => !_.selected);
    this.dumpData();
  }

  removeObject({ shape, outline }: DrawObject) {
    shape.off().remove();
    outline.off().remove();
  }

  download() {
    this.objects.forEach(this.deselect);
    saveSvgAsPng(this.svgRef.nativeElement, 'mdo-drawing.png');
    this.changeDetector.detectChanges();
  }

  dumpData() {
    const data: DrawDataObject[] = this.objects.map(object => ({
      toolId: object.toolId,
      payload: this.toolHandlers[object.toolId].getData(object)
    }));
    this.data = data;
    this.dataChange.next(data);
    this.changeDetector.detectChanges();
  }

  renderData(data: DrawDataObject[]) {
    data
      .filter(_ => !!this.toolHandlers[_.toolId])
      .forEach(object => {
        let shape: Shape = this.toolHandlers[object.toolId].renderData(object);
        this.addObject(shape, object.toolId);
      });
  }

  async preloadImages(def: DrawDef): Promise<DrawImageMap> {
    let urls: string[] = [def.background];
    const result: DrawImageMap = {};
    if (def.backgrounds) {
      urls = urls.concat(def.backgrounds);
    }
    if (def.toolbar) {
      urls = urls.concat(def.toolbar.filter(_ => !!_.src).map(_ => _.src));
    }

    const images = await Promise.all(urls.map(_ => this.loadImage(_)));
    result['background'] = images.shift();

    if (def.backgrounds) {
      def.backgrounds.forEach((bg, idx) => (result['background' + idx] = images.shift()));
    }

    if (def.toolbar) {
      def.toolbar.filter(_ => !!_.src).forEach(tool => (result[tool.toolId] = images.shift()));
    }
    return result;
  }

  loadImage = (url: string): Promise<Image> => {
    return new Promise(resolve => {
      const image = this.draw.image(url, () => resolve(image));
    });
  };

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }
}
