import { Container, Image, Shape } from '@svgdotjs/svg.js';
import { DrawXY, DrawTool, DrawObject, DrawDataObject } from '../../../draw.models';
import { DrawHandler, DrawHandlerInterface } from './draw-handler';

export class DrawImageHandler extends DrawHandler implements DrawHandlerInterface {
  _cursor: Shape;
  constructor(draw: Container, tool: DrawTool, source: Image) {
    super(draw, tool, source);

    source.hide();
    this.draw.add(source);
    this._cursor = source.clone();
    this.draw.add(this._cursor);
  }

  handleMouseEnter({ x, y }) {
    this._cursor.center(x, y);
    this._cursor.show();
  }

  handleMouseMove({ x, y }) {
    this._cursor.center(x, y);
  }

  handleMouseDown({ x, y }: DrawXY) {
    const image = this.addImage({ x, y });
    this.onShape$.next(image);
  }

  handleMouseLeave() {
    this._cursor.hide();
  }

  addImage({ x, y }: DrawXY): Image {
    const image = this.source
      .clone()
      .center(x, y)
      .show();
    this.draw.add(image);
    return image;
  }

  renderData(object: DrawDataObject) {
    return this.addImage(object.payload);
  }

  getData(object: DrawObject) {
    return {
      x: object.shape.cx(),
      y: object.shape.cy()
    };
  }
}
