import { Shape, Image, Path } from '@svgdotjs/svg.js';

// definition for draw component
// backround and tools
export interface DrawDef {
  // active background
  background: string;
  // list of backgrounds
  backgrounds?: Array<string>;
  // tools to show in topbar
  toolbar: DrawTool[];
  // preselected tool in toolbar
  activeTool?: number;
  // whether to hide toolbar
  toolbarHidden?: boolean;
  // expand debug info
  debug?: boolean;
}

export enum DrawToolKinds {
  ARROW = 'ARROW',
  IMAGE = 'IMAGE',
  BRUSH = 'BRUSH'
}

export interface DrawTool {
  // unique tool id
  toolId: string;
  // kind to select handler
  kind: DrawToolKinds;
  // options
  src?: string;
  icon?: string;
  color?: string;
  width?: number;
}

export interface DrawObject {
  toolId: string;
  shape: Shape | Image | Path;
  outline: Shape;
  selected: boolean;
}

export interface DrawXY {
  x: number;
  y: number;
}

export interface DrawDataObject {
  toolId: string;
  payload: any;
}

export type DrawImageMap = { [key: string]: Image };
