import { Container, Shape, Image } from '@svgdotjs/svg.js';
import { DrawTool, DrawXY, DrawDataObject, DrawObject } from '../../../draw.models';
import { Subject } from 'rxjs';

export interface DrawHandlerInterface {
  draw: Container;
  tool: DrawTool;
  source: Image;
  onShape$: Subject<Shape>;

  render(object: DrawDataObject): void;
  handleMouseEnter(xy: DrawXY): void;
  handleMouseDown(xy: DrawXY): void;
  handleMouseMove(xy: DrawXY): void;
  handleMouseUp(xy: DrawXY): void;
  handleMouseLeave(xy: DrawXY): void;

  renderData(object: DrawDataObject): Shape;
  getData(object: DrawObject): any;
  destory(): void;
}

// partial realization of common stuff for han all handlers
export abstract class DrawHandler implements DrawHandlerInterface {
  draw: Container;
  tool: DrawTool;
  source: Image;

  onShape$ = new Subject<Shape>();

  constructor(draw: Container, tool: DrawTool, source?: Image) {
    this.draw = draw;
    this.tool = tool;
    this.source = source;
  }

  // default empty implementations to not implement in every handler
  render(object: DrawDataObject) {}
  handleMouseEnter(xy: DrawXY) {}
  handleMouseDown(xy: DrawXY) {}
  handleMouseMove(xy: DrawXY) {}
  handleMouseUp(xy: DrawXY) {}
  handleMouseLeave(xy: DrawXY) {}

  renderData(object: DrawDataObject) {
    return null;
  }
  getData(object: DrawObject) {}

  destory() {}
}
