import { SVG, Line, Container, Image, Path, Shape } from '@svgdotjs/svg.js';
import { DrawHandler, DrawHandlerInterface } from './draw-handler';
import { DrawXY, DrawTool, DrawDataObject, DrawObject } from 'src/app/draw/draw.models';

export class DrawArrowHandler extends DrawHandler implements DrawHandlerInterface {
  _pointerActive: boolean;
  _line: Path;
  _pointFrom: DrawXY;
  _pointTo: DrawXY;

  constructor(draw: Container, tool: DrawTool, source: Image) {
    super(draw, tool, source);

    source.hide();
    draw.add(source);
  }

  handleMouseMove({ x: x2, y: y2 }: DrawXY) {
    if (!this._pointerActive || !this._line) {
      return;
    }
    const { x: x1, y: y1 } = this._pointFrom;

    const distance = this.getDistance(x1, y1, x2, y2);

    if (distance < 10) {
      return;
    }
    this._line.plot(`M ${x1} ${y1} L ${x2} ${y2}`);

    const headAngle = this.getAngle(x1, y1, x2, y2);
    this.source
      .show()
      .center(x2, y2)
      .attr('transform', `rotate(${headAngle} ${x2} ${y2})`);
  }

  handleMouseDown({ x, y }: DrawXY) {
    this._pointerActive = true;
    this._pointFrom = { x, y };
    this._line = this.draw
      .path()
      .stroke({
        width: this.tool.width || 1,
        color: this.tool.color || 'black'
      })
      .fill('none');
  }

  handleMouseUp() {
    this._pointerActive = false;

    const group = this.draw.group();
    group.add(this._line);
    group.add(this.source.clone());
    this.source.hide();
    this.onShape$.next(group);
  }

  renderData(object: DrawDataObject) {
    const group = this.draw.group();
    const line = this.draw
      .path()
      .stroke({
        width: this.tool.width || 1,
        color: this.tool.color || 'black'
      })
      .fill('none')
      .plot(object.payload);
    group.add(line);

    const [[, x1, y1], [, x2, y2]] = line.array();

    const headAngle = this.getAngle(x1, y1, x2, y2);
    const head = this.source
      .clone()
      .show()
      .center(x2, y2)
      .attr('transform', `rotate(${headAngle} ${x2} ${y2})`);

    group.add(head);
    return group;
  }

  getData(object: DrawObject) {
    const path = object.shape.first() as Path;
    return path.node.getAttribute('d');
  }

  getAngle(x1: number, y1: number, x2: number, y2: number) {
    return Math.round((Math.atan2(y2 - y1, x2 - x1) * 180) / Math.PI);
  }

  getDistance(x1: number, y1: number, x2: number, y2: number) {
    return Math.sqrt((x1 - x2) ** 2 + (y1 - y2) ** 2);
  }
}
