import { Container, Path, Shape, Image } from '@svgdotjs/svg.js';
import { DrawXY, DrawTool, DrawDataObject, DrawObject } from '../../../draw.models';
import { DrawHandler, DrawHandlerInterface } from './draw-handler';

export class DrawBrushHandler extends DrawHandler implements DrawHandlerInterface {
  _pointerActive: boolean;
  _cursor: Shape;
  _path: Path;
  _points: DrawXY[];

  constructor(draw: Container, tool: DrawTool, source: Image) {
    super(draw, tool, source);
    this._cursor = draw
      .circle(tool.width || 1)
      .fill(tool.color || 'black')
      .hide();
  }

  handleMouseEnter({ x, y }) {
    this._cursor.center(x, y);
    this._cursor.show();
  }

  handleMouseMove({ x, y }: DrawXY) {
    this._cursor.center(x, y);
    if (!this._pointerActive || !this._path) {
      return;
    }

    this._points.push({ x, y });
    const plot = this._points
      .map((point, idx) => {
        return idx === 0
          ? `M ${point.x} ${point.y}`
          : idx === 1
          ? `L ${point.x} ${point.y}`
          : ` ${point.x} ${point.y}`;
      })
      .join('');
    this._path.plot(plot);
  }

  handleMouseDown() {
    this._pointerActive = true;
    this._points = [];
    this._path = this.draw
      .path()
      .stroke({
        width: this.tool.width || 1,
        color: this.tool.color || 'black'
      })
      .fill('none');
  }

  handleMouseUp() {
    this._pointerActive = false;
    this.onShape$.next(this._path);
  }

  handleMouseLeave() {
    this._cursor.hide();
  }

  renderData(object: DrawDataObject) {
    return this.draw
      .path()
      .stroke({
        width: this.tool.width || 1,
        color: this.tool.color || 'black'
      })
      .fill('none')
      .plot(object.payload);
  }

  getData(object: DrawObject) {
    return (object.shape as Path).node.getAttribute('d');
  }
}
